.menu-list {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 0.75rem;

	li {
		display: inline-block;
		margin-left: 30px;

		&:first-child {
			margin-left: 0;
		}
	}

	a {
		color: #d0d0d0;
		text-transform: uppercase;
		transition: $transition-base;
		border-bottom: 1px solid transparent;

		&.active {
			color: $white;

			&:hover {
				border-bottom: 1px solid transparent;
			}
		}

		&:hover {
			border-bottom: 1px solid #d0d0d0;
		}
	}
}