.bg-big-circle {
	width: 1000px;
	height: 1000px;
	background-color: transparent;
	border-radius: 50%;
	border: 120px solid rgba($white, 0.1);

	@include max-w(1199) {
		width: 900px;
		height: 900px;
		border-width: 100px;
	}

	@include max-w(991) {
		width: 800px;
		height: 800px;
	}

	@include max-w(767) {
		width: 600px;
		height: 600px;
		border-width: 70px;
	}

	@include max-w(575) {
		width: 450px;
		height: 450px;
		border-width: 50px;
	}
}

.bg-medium-circle {
	width: 715px;
	height: 715px;
	background-color: transparent;
	border-radius: 50%;
	border: 120px solid rgba($white, 0.1);

	@include max-w(1199) {
