// MOUSE PARALLAX FIGURES
.background-figures-scene {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 9;

	&__item {
		width: 100%;
		height: 100%;
	}

	&__figure {
		position: absolute;
	}
}

.small-circle-figure {
	width: 25px;
	height: 25px;
	border: 2px solid rgba($white, 0.2);
	background-color: transparent;
	border-radius: 50%;
}

.small-square-figure {
	width: 20px;
	height: 20px;
	border: 2px solid transparent;
	background-color: transparent;

	&.-white {
		border-color: $white;
	}

	&.-opacity {
		border-color: rgba($white, 0.2);
	}

	&.-rotate-45 {
		transform: rotate(-45deg);
	}

	&.-lg {
		width: 25px;
		height: 25px;
	}
}

.dashed-circle-figure {
	border: 2px dashed $white;
	background-color: transparent;
	border-radius: 50%;

	&.-md {
		width: 120px;
		height: 120px;
	}

	&.-lg {
		width: 190px;
		height: 190px;
	}
}

.small-triangle-figure {
	width: 30px;
	display: inline-block;

	&.-rotate45 {
		transform: rotate(-45deg);
	}
}

.zigzag4-figure {
	width: 70px;
}

.zigzag3-figure {
	width: 10px;
}

.cross-figure {
	width: 22px;
}