.testimonials-item {
	text-align: center;
	margin-bottom: 65px;
	padding: 0 50px;

	@include max-w(991) {
		margin-bottom: 30px;
	}

	@include max-w(767) {
		margin-bottom: 0;
	}

	@include max-w(575) {
		padding: 0;
		margin-bottom: 15px;
	}

	&__photo {
		margin: 0 auto 30px;
	}

	&__title {
		margin-bottom: 20px;
	}

	&__text {
		max-width: 620px;
		margin: 0 auto;

		> p:before {
			content: "";
			display: inline-block;
			padding-right: 5px;
		}

		> p:after {
			content: "";
			display: inline-block;
			padding-left: 5px;
		}
	}
}

.testimonials-photo {
	max-width: 120px;

	@include max-w(767) {
		max-width: 100px;
	}
}