.plan-item {
	background-color: #F3F3F3;
	text-align: center;
	padding: 65px 80px;
	height: 100%;

	@include max-w(1199) {
		padding-left: 50px;
		padding-right: 50px;
	}

	@include max-w(991) {
		padding: 30px 15px;
	}

	@include max-w(575) {
		padding: 15px;
	}

	&__title {
		margin-bottom: 30px;

		@include max-w(767) {
			margin-bottom: 20px;
		}
	}

	&__price {
		margin-bottom: 40px;

		@include max-w(767) {
			margin-bottom: 20px;
		}
	}

	&__description {
		margin-bottom: 40px;

		@include max-w(575) {
			margin-bottom: 20px;
		}
	}

	&.-white {
		background-color: $white;
		box-shadow: 0px 0px 50px -10px rgba($black, 0.15);
		position: relative;
		z-index: 1;

		.plan-item__price {
			.price-text__currency, .price-text__value {
				color: $primary;
			}
		}
	}
}