.video-modal {
	.modal-dialog {
		margin: 0;
	}

	&__close-icon {
		position: absolute;
		top: -25px;
		right: -25px;
		font-size: 22px;
		z-index: 100;
		cursor: pointer;

		@include max-w(575) {
			top: -30px;
			right: 0;
		}

		&:before {
			color: $white;
			transition: $transition-base;
		}

		&:hover {
			&:before {
				color: $primary;
			}
		}
	}
}

.modal-open .modal {
	overflow-y: hidden !important;
}