@media screen and (max-width: 991px) {

	body {
		width: 100%;
		height: 100%;
	}

	.slideout-menu {
		position: fixed;
		top: 0;
		bottom: 0;
		width: 256px;
		min-height: 100vh;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		z-index: 0;
		display: none;
	}

	.slideout-menu-left {
		left: 0;
	}

	.slideout-menu-right {
		right: 0;
	}

	.slideout-panel {
		position: relative;
		z-index: 1;
		will-change: transform;
		background-color: $body-bg;
		min-height: 100vh;
	}

	.slideout-open,
	.slideout-open body,
	.slideout-open .slideout-panel {
		overflow: hidden;
	}

	.slideout-open .slideout-menu {
		display: block;
	}

	// overlay
	.slideout-panel {
		position: relative;

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba($black, 0.5);
			z-index: 99;
			transition: 0.5s;
			opacity: 0;
			visibility: hidden;
		}

		&.-panel-open {
			&:before {
				visibility: visible;
				opacity: 1;
			}
		}
	}

	.side-menu {
		background-color: $purple;
		border-left: 2px solid $gray-500;

		&__title {
			text-align: center;
			padding: 15px;
			height: 77px;
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			align-items: center;
			justify-content: center;
			border-bottom: 1px solid rgba($white, 0.08);

			@include max-w(767) {
				height: 67px;
			}
		}

		.menu-list {
			display: block;
			font-size: 1.2rem;
			padding: 30px;

			li {
				margin-left: 0;
				display: block;
				margin-bottom: 10px;
			}

			a {
				display: inline-block;
			}
		}
	}
}