.blog-item {
	margin-bottom: 20px;
	padding: 0 15px;

	@include max-w(575) {
		margin-bottom: 0;
	}

	&__photo {
		display: block;
		position: relative;
		padding-bottom: 67%;
		margin-bottom: 25px;
		overflow: hidden;
		cursor: pointer;

		> span {
			display: block;
			transform: scale(1);
			transition: 0.7s;
		}

		&:hover {
			> span {
				transform: scale(1.1);
			}
		}
	}

	&__title {
		font-family: 'Montserrat', sans-serif;
		margin-bottom: 10px;

		span {
			cursor: pointer;
			transition: $transition-base;

			&:hover {
				color: $primary;
			}
		}
	}

	&__date {
		margin-bottom: 10px;
	}
}