.hover-item {
	position: relative;
	display: block;
	overflow: hidden;
	padding-bottom: 103%;
	color: $white;
	text-align: center;

	&:hover {
		.hover-item__hover-block {
			opacity: 1;
		}

		.hover-item__hover-icon {
			transform: scale(1);
		}
	}

	&__hover-block {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		z-index: 5;
		padding: 10px;
		background-color: rgba($primary, 0.9);
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		opacity: 0;
		transition: 0.8s;
	}

	&__name {
		font-family: 'Montserrat', sans-serif;
		color: $white;
		font-weight: 500;
		margin-bottom: 20px;
		line-height: 1.5;

		@include max-w(991) {
			font-size: 1rem;
			margin-bottom: 10px;
		}

		@include max-w(575) {
			font-size: 0.75rem;
		}
	}

	&__hover-icon {
		color: $white;
		font-size: 2.3rem;
		transform: scale(0.5);
		transition: 0.5s;

		@include max-w(991) {
			font-size: 2rem;
		}

		@include max-w(575) {
			font-size: 1.5rem;
		}
	}

	&__position {
		color: $white;
		margin-bottom: 20px;

		p {
			margin-bottom: 0;
		}
	}

	&.-team-item {
		.hover-item__name {
			margin-bottom: 0;
		}
	}
}