.footer {
	background-color: $black-section;

	&.section {
		@include max-w(991) {
			padding-bottom: 50px;
		}

		@include max-w(767) {
			padding-bottom: 30px;
		}

		@include max-w(575) {
			padding-bottom: 10px;
		}
	}

	&__copyright {
		max-width: 260px;

		@include max-w(575) {
			max-width: 100%;
		}
	}

	&__pages-list {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-wrap: wrap;

		li {
			width: 50%;
		}
	}

	&__item {
		@include max-w(991) {
			margin-bottom: 30px;
		}

		@include max-w(575) {
			text-align: center;
			margin-bottom: 20px;
		}
	}
}