.quantity-item {
	position: relative;
	padding-left: 65px;

	@include max-w(991) {
		margin-bottom: 35px;
		padding-left: 0;
		text-align: center;
	}

	&__icon {
		display: inline-block;
		width: 38px;
		position: absolute;
		top: -4px;
		left: 0;

		@include max-w(991) {
			position: static;
			margin-bottom: 10px;
		}
	}

	&__title {
		margin-bottom: 5px;
		font-weight: 500;
	}

	&__description {
		p {
			margin-bottom: 0;
		}
	}
}