.features-item {
	text-align: center;
	padding: 40px 20px 35px;
	cursor: pointer;
	transition: $transition-base;

	@include max-w(991) {
		padding: 20px 15px;
	}

	@include max-w(575) {
		padding: 15px;
	}

	&__icon {
		display: inline-block;
		width: 50px;
		margin-bottom: 20px;

		.animation-svg {
			transition: $transition-base;
		}
	}

	&__description {
		transition: $transition-base;
	}

	&:hover {
		background-color: $blue;
		color: $white;

		.features-item__icon {
			.animation-svg {
				stroke: $white;
			}
		}

		.features-item__description {
			color: $white;
		}
	}
}

.features-item-with-img {

	@include max-w(767) {
		margin-bottom: 40px;
	}

	&__img {
		position: relative;
		padding-bottom: 67%;
		margin-bottom: 30px;
		overflow: hidden;

		@include max-w(991) {
			margin-bottom: 20px;
		}
	}

	&__description-block {
		border-left: 2px solid $primary;
		padding-left: 20px;

		@include max-w(991) {
			padding-left: 15px;
		}
	}

	&__description-text {
		max-width: 300px;

		p {
			margin-bottom: 0;
		}
	}
}