.form-control {
	@include placeholder-opacity(0.5s);

	@include max-w(767) {
		padding: 10px 15px;
	}
}

textarea {
	height: 150px;
}

.form-group {
	label.error {
		color: #b52d2d;
		padding-top: 5px;
	}
}

.successful-text {
	color: #347334;
	padding-top: 1rem;
	opacity: 0;
	transition: 0.5s;

	&.-show {
		opacity: 1;
	}
}