.section {
	padding: 100px 0;
	overflow: hidden;

	@include max-w(1199) {
		padding: 80px 0;
	}

	@include max-w(991) {
		padding: 65px 0;
	}

	@include max-w(767) {
		padding: 50px 0;
	}

	@include max-w(575) {
		padding: 30px 0;
	}

	&.-centering {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&.-full-height {
		height: 100vh;

		@include max-w(767) {
			height: auto;
		}
	}

	&-description {
		max-width: 505px;
	}

	&-gradient {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
	}

	&-big-figure {
		position: absolute;
		z-index: 2;
	}

	&-bg-image {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 3;
		background-repeat: no-repeat;
	}

	// SECTION WELCOME
	&-welcome {
		@include rbg;
		position: relative;
		padding-top: 125px;
		text-align: center;

		@include max-w(767) {
			padding-top: 0;
		}

		&.-full-height {
			height: 100vh;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: 8;
			background-color: rgba(#291644, 0.9);
		}

		// figures position
		&__figures-scene {
			.small-circle-figure {
				top: 20%;
				left: 15%;

				@include max-w(991) {
					left: 10%;
				}

				@include max-w(575) {
					top: 15%;
					left: 5%;
				}
			}

			.small-square-figure {
				&.-white {
					left: 5%;
					top: 35%;

					@include max-w(991) {
						left: 2%;
					}

					@include max-w(575) {
						display: none;
					}
				}

				&.-opacity {
					top: 60%;
					right: 25%;

					@include max-w(991) {
						right: 20%;
					}

					@include max-w(575) {
						right: 10%;
						top: auto;
						bottom: 25%;
					}
				}
			}

			.dashed-circle-figure {
				&.-md {
					left: -80px;
					top: 50%;

					@include max-w(575) {
						top: auto;
						bottom: 15%;
					}
				}

				&.-lg {
					right: -100px;
					bottom: 10%;

					@include max-w(991) {
						right: -130px;
					}

					@include max-w(575) {
						bottom: -30px;
					}
				}
			}

			.small-triangle-figure {
				top: 60%;
				left: 20%;

				@include max-w(991) {
					left: 13%;
				}
			}

			.zigzag3-figure {
				bottom: 10%;
				left: 10%;

				@include max-w(991) {
					left: 8%;
				}

				@include max-w(575) {
					display: none;
				}
			}

			.cross-figure {
				top: 20%;
				right: 20%;

				@include max-w(575) {
					top: 15%;
				}
			}

			.small-triangle-figure-2 {
				left: auto;
				right: 0;
				top: 20%;
			}

			.zigzag4-figure {
				top: 50%;
				right: 5%;

				@include max-w(991) {
					right: 2%;
				}

				@include max-w(575) {
					display: none;
				}
			}
		}

		.content-block {
			margin-top: -150px;

			@include max-w(767) {
				margin-top: 0;
			}
		}

		.section-welcome__app-photo {
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			z-index: 11;
			font-size: 0;
			text-align: center;

			img {
				max-width: 875px;
				width: 100%;

				@include max-w-and-max-h(1199, 770) {
					max-width: 600px;
				}

				@include max-w(767) {
					max-width: 550px;
				}
			}

		}

		.section-welcome__description {
			max-width: 655px;
			margin: 0 auto;

			@include max-w-and-max-h(1199, 770) {
				p {
					margin-bottom: 15px;
				}
			}
		}
	}

	// SECTION FULLPAGE 1
	&-fullpage-1 {

		@include max-w(767) {
			padding-bottom: 350px;
		}

		@include max-w(575) {
			padding-bottom: 320px;
		}

		.section-fullpage-1__gradient {
			background: linear-gradient(90deg, #FF1558, #FF6A27);
		}

		.section-fullpage-1__bg-figure {
			right: -150px;
			bottom: -300px;

			@include max-w(1400) {
				bottom: -400px;
				right: -250px;
			}

			@include max-w(991) {
				right: -200px;
				bottom: -200px;
			}

			@include max-w(575) {
				right: -150px;
				bottom: -150px;
			}
		}

		.section-fullpage-1__bg-image {
			background-position: right bottom;

			@include max-w(1400) {
				background-position: right 0 bottom -50px;
			}

			@include max-w(1199) {
				background-size: 600px;
				background-position: right -100px bottom 0;
			}

			@include max-w(991) {
				background-size: 500px;
				background-position: right -100px bottom 0;
			}

			@include max-w(767) {
				background-size: 450px;
				background-position: right -120px bottom 0;
			}

			@include max-w(575) {
				background-size: 350px;
				background-position: right -80px bottom 0;
			}
		}

		.section-fullpage-1__btn-wrap {
			.btn {
				margin-right: 20px;

				@include max-w(991) {
					margin-bottom: 15px;
				}

				@include max-w(767) {
					display: block;
					max-width: 180px;
				}
			}
		}
	}

	// SECTION FULLPAGE 2
	&-fullpage-2 {

		@include max-w(767) {
			padding-bottom: 300px;
		}

		.section-fullpage-2__gradient {
			background: linear-gradient(45deg, #00B2BE, #640BA6);
		}

		.section-fullpage-2__bg-figure {
			top: -500px;
			left: -300px;

			@include max-w(1500) {
				left: -400px;
			}

			@include max-w(767) {
				top: auto;
				left: auto;
				bottom: -350px;
				right: -200px;
			}

			@include max-w(575) {
				bottom: -200px;
				right: -200px;
			}
		}

		.section-fullpage-2__bg-image {
			background-position: left 20% center;

			@include max-w(1400) {
				background-size: 450px;
				background-position: left 10% center;
			}

			@include max-w(991) {
				background-size: 350px;
				background-position: left 8% center;
			}

			@include max-w(767) {
				background-size: 300px;
				background-position: right 50px bottom 30px;
			}

			@include max-w(575) {
				background-size: 250px;
				background-position: right 30px bottom 30px;
			}
		}
	}

	// SECTION FULLPAGE 3
	&-fullpage-3 {

		@include max-w(767) {
			padding-bottom: 350px;
		}

		@include max-w(575) {
			padding-bottom: 300px;
		}

		.section-fullpage-3__gradient {
			background: linear-gradient(45deg, #009C57, #A5A21D);
		}

		.section-fullpage-3__bg-figure {
			bottom: -250px;
			right: -100px;

			@include max-w(991) {
				bottom: -200px;
				right: -200px;
			}

			@include max-w(767) {
				bottom: -100px;
				right: -100px;
			}

			@include max-w(575) {
				bottom: -80px;
				right: -160px;
			}
		}

		.section-fullpage-3__bg-image {
			background-position: right 20% bottom -170px;

			@include max-w(991) {
				background-size: 250px;
			}

			@include max-w(767) {
				background-size: 220px;
				background-position: right 20% bottom -130px;
			}

			@include max-w(575) {
				background-size: 180px;
				background-position: right 45% bottom -100px;
			}
		}
	}

	// SECTION FEATURES
	&-features {
		.section-features__description {
			margin-bottom: 60px;

			@include max-w(991) {
				margin-bottom: 40px;
			}

			@include max-w(575) {
				margin-bottom: 20px;
			}
		}

		.section-features__items-block {
			padding-bottom: 55px;
			border-bottom: 1px solid #e3e3e3;

			@include max-w(991) {
				padding-bottom: 30px;
			}

			@include max-w(575) {
				padding-bottom: 20px;
			}
		}

		.section-features__items-with-img-block {
			padding-top: 80px;

			.row {
				> div:last-child {
					.features-item-with-img {
						margin-bottom: 0;
					}
				}
			}

			@include max-w(767) {
				padding-top: 60px;
			}

			@include max-w(575) {
				padding-top: 50px;
			}
		}
	}

	// SECTION SERVICES
	&-services {
		background-color: $black-section;
		padding-top: 140px;
		padding-bottom: 140px;
		background-image: url(../img/dots-bg-pattern.png), url(../img/dots-bg-pattern.png);
		background-position: top left, bottom left;
		background-repeat: repeat-x;

		@include max-w(1199) {
			padding-bottom: 120px;
		}

		@include max-w(991) {
			padding-top: 100px;
			padding-bottom: 80px;
		}

		@include max-w(767) {
			padding-top: 80px;
			padding-bottom: 60px;
		}

		@include max-w(575) {
			padding-top: 65px;
		}

		.section-title {
			margin-bottom: 30px;

			@include max-w(767) {
				margin-bottom: 15px;
			}
		}

		.section-services__description {
			margin-bottom: 60px;

			@include max-w(575) {
				margin-bottom: 40px;
			}
		}

		.section-services__main-img {
			max-width: 370px;
			padding-right: 30px;

			@include max-w(767) {
				margin: 0 auto;
				padding-right: 0;
				max-width: 280px;
			}
		}

		@include max-w(767) {
			.section-services__list-block {
				margin-bottom: 60px;
			}
		}
	}

	// SECTION QUANTITY
	&-quantity {
		background-color: $primary;

		@include max-w(575) {
			.row {
				> div:last-child {
					.quantity-item {
						margin-bottom: 0;
					}
				}
			}
		}
	}

	// SECTION ADVANTAGES
	&-advantages {
		.row {
			> div:last-child {
				.advantages-item {
					margin-bottom: 0;
				}
			}
		}
	}

	// SECTION PROJECTS
	&-projects {
		background-color: #F3F3F3;

		.section-projects__items-block {
			margin-bottom: 50px;

			@include max-w(991) {
				margin-bottom: 30px;
			}

			@include max-w(575) {
				margin-bottom: 10px;

				[class*='col-'] {
					padding: 0 5px;
				}

				.row {
					margin-left: -5px;
					margin-right: -5px;
				}
			}
		}

		.section-projects__hover-item {
			cursor: pointer;
			margin-bottom: 30px;

			@include max-w(575) {
				margin-bottom: 10px;
			}
		}
	}

	// SECTION VIDEO
	&-video {
		.section-video__title {
			max-width: 640px;
			margin: 0 auto 75px;

			@include max-w(991) {
				margin-bottom: 50px;
			}

			@include max-w(767) {
				margin-bottom: 30px;
			}
		}

		.section-video__video-block {
			position: relative;
			overflow: hidden;

			.container {
				position: relative;
				z-index: 2;
			}
		}

		.section-video__bg-text {
			font-family: 'Montserrat', sans-serif;
			font-weight: 700;
			font-size: 12.5rem;
			letter-spacing: 1px;
			text-transform: uppercase;
			color: #efefef;
			@include absolute-center;
			white-space: nowrap;

			@include max-w(991) {
				font-size: 9rem;
			}

			@include max-w(767) {
				font-size: 7rem;
			}

			@include max-w(575) {
				display: none;
			}
		}
	}

	// SECTION BLACK ADVANTAGES
	&-black-advantages {
		background-color: #0C0C0C;

		.black-advantages-item__text {
			margin-bottom: 30px;

			@include max-w(575) {
				margin-bottom: 15px;
			}
		}
	}

	// SECTIONS PLANS
	&-plans {
		padding-bottom: 0;

		.section-plans__items-block {
			position: relative;
			z-index: 5;
		}

		.container {
			@include max-w(767) {
				max-width: 100%;
			}
		}
	}

	// SECTION TESTIMONIALS
	&-testimonials {
		background-color: #2B1241;
		padding-top: 320px;
		margin-top: -210px;
		position: relative;
		overflow: hidden;
		background-image: url(../img/dots-bg-pattern.png);
		background-position: bottom left;
		background-repeat: repeat-x;

		@include max-w(991) {
			margin-top: -180px;
			padding-top: 250px;
		}

		@include max-w(575) {
			margin-top: -320px;
			padding-top: 370px;
		}

		@include max-w(400) {
			margin-top: -350px;
			padding-top: 390px;
		}

		.section-testimonials__bg-figure-top {
			top: -360px;
			right: -175px;

			@include max-w(991) {
				top: -400px;
				right: -250px;
			}

			@include max-w(767) {
				top: -250px;
				right: -200px;
			}

			@include max-w(575) {
				top: 0;
			}
		}

		.section-testimonials__bg-figure-bottom {
			bottom: -350px;
			left: -350px;

			@include max-w(767) {
				bottom: -250px;
				left: -250px;
			}
		}

		.testimonials-slider {
			@include max-w(767) {
				margin-bottom: 60px;
			}

			.slick-arrow {
				@include max-w(575) {
					transform: none;
					top: 35px;
				}
			}
		}
	}

	// SECTION LOGOS
	&-logos {
		padding-bottom: 0;
	}

	// SECTION TEAM
	&-team {
		.container-fluid {
			padding: 0;
		}
	}

	// SECTION BLOG
	&-blog {
		.section-blog__items-block {
			margin: 0 -15px;
		}
	}

	// SECTION DOWNLOAD
	&-download {
		@include rbg;
		position: relative;
		padding-top: 120px;
		padding-bottom: 300px;

		@include max-w(1199) {
			padding-top: 90px;
			padding-bottom: 250px;
		}

		@include max-w(991) {
			padding-top: 60px;
			padding-bottom: 200px;
		}

		@include max-w(767) {
			padding-bottom: 170px;
		}

		@include max-w(575) {
			padding-top: 30px;
			padding-bottom: 130px;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: 8;
			background-color: rgba(#291644, 0.9);
		}

		.section-download__main-text {
			text-align: center;
			max-width: 590px;
			margin: 0 auto;
		}

		// figures position
		&__figures-scene {

			.small-square-figure {
				&.-white {
					top: 50px;
					left: 70px;

					@include max-w(991) {
						left: 30px;
					}

					@include max-w(767) {
						display: none;
					}
				}

				&.-opacity {
					bottom: 40%;
					right: 20%;

					@include max-w(991) {
						right: 15%;
					}

					@include max-w(767) {
						right: 5%;
					}
				}
			}

			.small-circle-figure {
				bottom: 40%;
				left: 30%;

				@include max-w(991) {
					left: 15%;
				}

				@include max-w(767) {
					display: none;
				}
			}

			.dashed-circle-figure {
				&.-md {
					left: -50px;
					bottom: 20%;

					@include max-w(991) {
						left: -70px;
						bottom: 10%;
					}
				}

				&.-lg {
					right: -80px;
					bottom: 10%;

					@include max-w(991) {
						right: -120px;
					}

					@include max-w(767) {
						bottom: -50px;
					}
				}
			}

			.small-triangle-figure-1 {
				left: 15%;
				top: 20%;

				@include max-w(991) {
					left: 10%;
				}

				@include max-w(767) {
					top: 10px;
					left: 5px;
				}
			}

			.small-triangle-figure-2 {
				right: 5%;
				top: 10%;

				@include max-w(767) {
					display: none;
				}
			}

			.zigzag3-figure {
				top: 40%;
				left: 5%;

				@include max-w(767) {
					display: none;
				}
			}

			.zigzag4-figure {
				right: 5%;
				top: 50%;

				@include max-w(991) {
					top: 40%;
				}

				@include max-w(767) {
					display: none;
				}
			}

			.cross-figure {
				top: 30px;
				right: 25%;

				@include max-w(991) {
					right: 15%;
				}

				@include max-w(767) {
					right: 20px;
					top: 10px;
				}
			}
		}
	}

	// SECTION STAY IN TOUCH
	&-stay-in-touch {
		padding-top: 0;
		overflow: inherit;
		background-color: $primary;

		.section-stay-in-touch__subscribe {
			overflow: inherit;
			position: relative;
			z-index: 11;
			margin-top: -180px;
			margin-bottom: 110px;

			@include max-w(1199) {
				margin-top: -160px;
			}

			@include max-w(991) {
				margin-top: -140px;
				margin-bottom: 80px;
			}

			@include max-w(767) {
				margin-top: -120px;
				margin-bottom: 40px;
			}

			@include max-w(575) {
				margin-top: -100px;
				margin-bottom: 30px;
			}
		}

		.subscribe-block-form {
			.input-group {
				@include max-w(575) {
					display: block;
				}

				> .form-control {
					@include max-w(575) {
						width: 100%;
						margin-bottom: 15px;
					}
				}

				.input-group-append .btn {
					@include max-w(575) {
						margin-left: auto;
						margin-right: auto;
					}
				}
			}
		}
	}

	// SECTION CONTACT US
	&-contact-us {
		position: relative;

		@include max-w(767) {
			padding-bottom: 50px;
		}

		@include max-w(575) {
			padding-bottom: 30px;
		}

		.section-contact-us__title {
			margin-bottom: 30px;

			@include max-w(767) {
				margin-bottom: 15px;
			}

			@include max-w(575) {
				margin-bottom: 10px;
			}
		}

		.section-contact-us__content {
			max-width: 520px;
			padding-right: 30px;

			@include max-w(767) {
				padding-right: 0;
			}
		}

		.section-contact-us__description {
			max-width: 375px;
			margin-bottom: 40px;

			@include max-w(575) {
				margin-bottom: 20px;
			}
		}

		.section-contact-us__map-wrap {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 50%;
			height: 100%;
			z-index: 1;

			@include max-w(767) {
				position: relative;
				width: auto;
				height: 300px;
				margin: 0 15px;
			}
		}
	}
}