.slick-prev, .slick-next {
	width: $slick-arrow-width;
	height: $slick-arrow-height;
	background-color: $slick-arrow-bg;
	z-index: 1;

	&:before {
		transition: 0.5s;
		font-size: $slick-arrow-size;
		font-weight: $slick-arrow-weight;
	}

	&:focus {
		&:before {
			color: rgba($slick-arrow-color, 0.6);
		}
	}

	&:hover {
		&:before {
			color: lighten($slick-arrow-color, 10%);
		}
	}
}

.slick-prev {
	left: 0;
}

.slick-next {
	right: 0;
}

// BOTTOM ARROWS
.bottom-slick-arrow {
	padding-bottom: 50px;

	.slick-prev, .slick-next {
		bottom: 0;
		top: auto;
		transform: none;

		&:before {
			color: rgba(#141414, 0.6);
			opacity: 1;
		}

		&:hover {
			&:before {
				color: rgba(#141414, 0.8);
			}
		}

		&:focus {
			&:before {
				color: rgba(#141414, 0.6);
				opacity: 1;
			}
		}
	}

	.slick-prev {
		left: calc(50% - 45px);
	}

	.slick-next {
		right: calc(50% - 45px);
	}
}