.single-project-modal {
	height: 100% !important;

	&.hasShadow {
		&:after {
			display: none;
		}
	}

	&__content {
		padding-top: 40px;
	}

	&__button-wrap {
		margin-bottom: 55px;

		@include max-w(767) {
			margin-bottom: 40px;
		}
	}

	&__description {
		max-width: 590px;
	}

	&__main-text {
		margin-bottom: 85px;

		@include max-w(1199) {
			margin-bottom: 40px;
		}

		@include max-w(575) {
			margin-bottom: 25px;
		}
	}

	&__photos {
		.container-fluid {
			padding: 0 30px;

			@include max-w(575) {
				padding: 0 15px;
			}
		}
	}

	&__main-photo {
		position: relative;
		padding-bottom: 670px;
		margin-bottom: 30px;

		@include max-w(991) {
			padding-bottom: 400px;
		}

		@include max-w(575) {
			padding-bottom: 250px;
			margin-bottom: 15px;
		}
	}
}

.project-info {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	&__title {
		flex: 0 0 auto;
		padding-right: 10px;
		font-weight: 600;
	}
}