.slick-dots {
	bottom: -25px;

	li {
		width: $slick-dots-width;
		height: $slick-dots-height;
		margin: 0 $slick-dots-margin-left-right;

		button {
			width: $slick-dots-width;
			height: $slick-dots-height;
			background-color: $white;
			padding: 0;
			opacity: 0.6;
			border-radius: 50%;
			transition: $transition-base;

			&:before {
				display: none;
			}
		}

		&:hover {
			button {
				opacity: 1;
			}
		}
	}

	li.slick-active {
		width: 22px;

		button {
			width: 22px;
			border-radius: 3px;
			opacity: 1;
		}
	}
}