@media only screen and (min-width: 992px) {

	// slides

	.fullpage-slider-wrap {
		background-color: $purple;
	}

	.fullpage-slider-box {
		position: relative;
		height: 100%;
		z-index: 90;
		overflow: hidden;

		&__item {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			transition: 1s;

			&.-active {
				opacity: 1;
				z-index: 10;
			}

			// ANIMATION
			.section-title {
				position: relative;
				bottom: 100px;
				opacity: 0;
				transition: 1.5s;
				transition-delay: 0s;
			}

			.section-description {
				position: relative;
				bottom: 50px;
				opacity: 0;
				transition: 1.5s;
				transition-delay: 0s;
			}

			.section-fullpage-1__btn-wrap, .section-fullpage-2__btn-wrap, .section-fullpage-3__btn-wrap {
				opacity: 0;
				transition: 1.5s;
				transition-delay: 0s;
			}

			.section-big-figure {
				transition: 2s;
				transform: translate(30%, 30%);
			}

			.section-fullpage-2__bg-figure {
				transform: translate(-30%, -30%);
			}

			&.-animation {
				.section-title {
					opacity: 1;
					bottom: 0;
					transition-delay: 0.5s;
				}

				.section-description {
					bottom: 0;
					opacity: 1;
					transition-delay: 0.5s;
				}

				.section-fullpage-1__btn-wrap, .section-fullpage-2__btn-wrap, .section-fullpage-3__btn-wrap {
					opacity: 1;
					transition-delay: 0.5s;
				}

				.section-big-figure {
					transform: translate(0, 0);
				}
			}
		}
	}

	// navigation
	.fullpage-slider-nav {
		position: fixed;
		top: 50%;
		right: 50px;
		transform: translate(0, -50%);
		z-index: 100;
		margin-right: 0;
		display: none;

		&__item {
			margin-bottom: 10px;
			padding-right: 20px;
			position: relative;
			cursor: pointer;

			&:hover {
				.fullpage-slider-nav-item__dot {
					opacity: 1;
				}
			}

			&.-active {
				.fullpage-slider-nav-item__text {
					opacity: 1;
				}
				.fullpage-slider-nav-item__dot {
					opacity: 1;
				}
			}
		}
	}

	.fullpage-slider-nav-item {

		&__text {
			display: block;
			opacity: 0;
			transition: 0.5s;
		}

		&__dot {
			display: inline-block;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background-color: $white;
			position: absolute;
			top: 50%;
			right: 0;
			opacity: 0.4;
			transform: translate(0, -50%);
			transition: 0.5s;
		}
	}

}

@media only screen and (max-width: 991px) {

	.fullpage-slider-item {
		position: relative !important;
		height: auto !important;
		opacity: 1 !important;
	}

	.fullpage-slider-nav {
		display: none;
	}

	.fullpage-slider-wrap {
		height: auto !important;
	}

}