.header {
	background-color: transparent;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	border-bottom: 1px solid rgba($white, 0.08);
	padding: 20px 0;
	transition: 0.5s;

	@include max-w(1400) {
		padding: 10px 0;
	}

	@include max-w(991) {
		.container {
			max-width: 100%;
		}
	}

	&.-fill-header {
		background-color: $purple;
	}

	&.-hide-header {
		transform: translate(0, -100%) !important;
	}

	&__wrap {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__menu-list-wrap {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		justify-content: flex-end;

		.btn {
			@include max-w(575) {
				min-width: auto;
			}
		}
	}

	&__btn {
		margin-left: 30px;
	}
}