.btn {
	font-family: 'Montserrat', sans-serif;

	&:focus {
		box-shadow: none;
	}

	@include max-w(991) {
		padding: 15px 10px;
	}

	@include max-w(767) {
		padding: 12px;
	}

	&-md {
		font-weight: 500;
		font-size: 0.75rem;
		text-transform: uppercase;
		letter-spacing: 0.9px;

		@include max-w(767) {
			font-size: 0.6rem;
		}
	}

	&.-w-165 {
		min-width: 165px;

		@include max-w(991) {
			min-width: 145px;
		}
	}

	&.-w-200 {
		min-width: 200px;

		@include max-w(991) {
			min-width: 170px;
		}
	}

	&.-with-icon-download {
		padding-top: 0.95rem;
		padding-bottom: 0.95rem;

		.btn__icon {
			margin-left: 5px;
			font-size: 1rem;

			@include max-w(767) {
				font-size: 0.75rem;
			}
		}

		.btn__text {
			position: relative;
			bottom: 2px;
		}
	}

	&.-with-icon-arrow {
		.btn__icon {
			margin-left: 15px;
			font-size: 10px;
		}
	}

	&.-tall-btn {
		padding-top: 1.45rem;
		padding-bottom: 1.45rem;

		@include max-w(767) {
			padding-top: 1rem;
			padding-bottom: 1rem;
		}
	}

	&.-left-icon-block {
		padding: 0;
		font-size: 0;
		text-align: center;

		.btn__icon {
			border-right: 1px solid rgba($white, 0.4);
			font-size: 1.2rem;
			padding: 0.65rem 0;
			min-width: 43px;
			vertical-align: middle;
			display: inline-block;

			@include max-w(767) {
				padding: 0.4rem 0;
			}
		}

		.btn__text {
			display: inline-block;
			padding: 0 0.5rem;
			font-size: 0.75rem;
			min-width: 130px;
			vertical-align: middle;
		}
	}

	&-blue {
		background-color: $blue;
		color: $white;

		&:hover {
			color: $white;
			background-color: #204BC7;
		}
	}

	&-primary {
		color: $white;

		&:hover {
			color: $white;
			background-color: #CF134E;
		}

		&:focus {
			box-shadow: none;
		}
	}

	&-outline-gray {
		color: $white;
		border-color: rgba(#fff, 0.4);

		&:hover {
			color: $white;
			border-color: $white;
		}
	}

	&-fixed-top {
		position: fixed;
		bottom: 50px;
		right: 50px;
		z-index: 100;
		display: inline-block;

		&.-disable {
			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
				background-color: rgba($black, 0.5);
			}
		}

		@include max-w(991) {
			right: 15px;
			bottom: 15px;
		}

		@include max-w(767) {
			bottom: 0;
			right: 0;
		}
	}

	&-scroll-top {
		display: inline-block;
		text-align: center;
		color: $white;
		cursor: pointer;
		opacity: 0;
		visibility: hidden;
		transition: $transition-base;

		&.-show {
			opacity: 1;
			visibility: visible;
		}

		.btn-scroll-top__icon {
			font-size: 2.1rem;
			line-height: 48px;
		}

		&.btn-md {
			width: 48px;
			height: 48px;
			font-size: 2.1rem;
		}
	}

	&-gray {
		background-color: rgba($gray-900, 0.5);

		&:hover {
			background-color: rgba($gray-900, 0.7);
		}
	}

	&-play {
		display: inline-block;
		border-radius: 50%;
		text-align: center;
		transform: scale(1);
		transition: $transition-base;
		cursor: pointer;

		&:hover {
			transform: scale(1.2);
		}

		.btn-primary {
			background-color: $primary;

			&:hover {
				background-color: #CF134E;
			}
		}

		&.btn-md {
			width: 70px;
			height: 70px;
			font-size: 1rem;

			@include max-w(991) {
				width: 55px;
				height: 55px;
			}
		}

		.btn-play__icon {
			line-height: 70px;
			position: relative;
			left: 2px;

			@include max-w(991) {
				line-height: 55px;
			}
		}
	}

	&-close {
		width: 60px;
		height: 60px;
		border: 1px solid #D2D2D2;
		color: #D2D2D2;
		text-align: center;
		background-color: $white;
		transition: 0.3s;

		> span {
			display: inline-block;
			line-height: 60px;
			font-size: 3rem;
			transform: rotate(0deg);
			transition: 0.3s;
		}

		&:hover {
			border-color: $gray-600;

			> span {
				transform: rotate(-90deg);
			}
		}
	}

	&-facebook {
		background-color: #2E599D;

		&:hover {
			background-color: darken(#2E599D, 10%);
		}
	}

	&-google {
		background-color: #E14C2F;

		&:hover {
			background-color: darken(#E14C2F, 10%);
		}
	}

	&-twitter {
		background-color: #00A3F9;

		&:hover {
			background-color: darken(#00A3F9, 10%);
		}
	}

	&-pinterest {
		background-color: #D30006;

		&:hover {
			background-color: darken(#D30006, 10%);
		}
	}

	&-social {
		color: $white;
		padding: 15px;
		transition: $transition-base;

		@include max-w(991) {
			padding: 15px 10px;
		}

		@include max-w(767) {
			padding: 10px 5px;
		}

		&:hover {
			color: $white;
		}

		.btn-social__icon {
			margin-right: 15px;
			font-size: 1.2rem;
			vertical-align: middle;

			@include max-w(991) {
				margin-right: 5px;
			}
		}

		.btn-social__text {
			vertical-align: middle;
		}
	}
}

.link {
	font-family: 'Montserrat', sans-serif;
	display: inline-block;
	transition: $transition-base;

	&-md {
		font-size: 0.75rem;
		text-transform: uppercase;
		letter-spacing: 0.9px;
		font-weight: 500;
	}

	&-white {
		color: $white;

		&:hover {
			color: $white;

			.link__icon {
				transform: scale(1.1);
				border-color: $white;
			}

			.link__text {
				border-bottom-color: $white;
			}
		}
	}

	&__icon {
		display: inline-block;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		border: 2px solid rgba($white, 0.4);
		margin-right: 15px;
		text-align: center;
		line-height: 40px;
		font-size: 0.7rem;
		transition: $transition-base;
		transform: scale(1);

		> span {
			position: relative;
			left: 2px;
			bottom: 1px;
		}
	}

	&__text {
		border-bottom: 1px solid transparent;
		transition: $transition-base;
	}
}