.black-advantages-item {
	background-color: #0C0C0C;

	&__content {
		padding: 65px 35px;

		@include max-w(1199) {
			padding: 50px 15px;
		}

		@include max-w(575) {
			padding: 30px 0;
		}
	}

	&__title {
		max-width: 190px;

		@include max-w(575) {
			max-width: 100%;
		}
	}

	&__img {
		height: 100%;

		@include max-w(575) {
			height: 300px;
		}
	}
}