.video-item {
	position: relative;
	display: block;
	padding-bottom: 61%;
	@include rbg;
	cursor: pointer;

	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#0C0C0C, 0.6);
	}

	&__btn-play {
		@include absolute-center;
		z-index: 10;
	}
}