.stay-in-touch-item {
	color: $white;
	position: relative;
	padding-left: 100px;

	@include max-w(991) {
		padding-left: 75px;
	}

	@include max-w(767) {
		padding-left: 0;
		margin-bottom: 30px;
		text-align: center;
	}

	&__icon {
		width: 75px;
		height: 75px;
		border: 2px solid $white;
		border-radius: 50%;
		position: absolute;
		top: 0;
		left: 0;

		@include max-w(991) {
			width: 60px;
			height: 60px;
			margin: 0 auto 15px;
		}

		@include max-w(767) {
			position: relative;
		}

		svg {
			width: 44px;
			@include absolute-center;

			@include max-w(991) {
				width: 35px;
			}
		}
	}

	&__text {
		max-width: 235px;

		@include max-w(767) {
			max-width: 100%;
		}
	}
}