.single-blog-modal {
	height: 100% !important;

	&.hasShadow {
		&:after {
			display: none;
		}
	}

	&__content {
		padding-top: 40px;
		padding-bottom: 70px;

		@include max-w(575) {
			padding-bottom: 40px;
		}
	}

	&__button-wrap {
		margin-bottom: 85px;

		@include max-w(1199) {
			margin-bottom: 60px;
		}

		@include max-w(991) {
			margin-bottom: 40px;
		}
	}

	&__date-block {
		font-weight: 700;
		margin-bottom: 55px;

		@include max-w(767) {
			margin-bottom: 30px;
		}
	}

	&__main-photo {
		position: relative;
		padding-bottom: 32%;
		margin: 0 30px 80px;

		@include max-w(1199) {
			margin-bottom: 50px;
		}

		@include max-w(767) {
			margin-bottom: 30px;
		}

		@include max-w(575) {
			margin-left: 0;
			margin-right: 0;
			padding-bottom: 50%;
		}
	}

	&__main-text {
		& > strong, & > i {
			font-family: 'Playfair Display', serif;
		}

		h1, h2, h3, h4, h5, h6 {
			font-family: 'Montserrat', sans-serif;
			font-weight: 700;
			font-size: 1.5rem;
			margin-bottom: 30px;
			letter-spacing: 0.12px;

			@include max-w(575) {
				font-size: 1.3rem;
				margin-bottom: 15px;
			}
		}
	}

	&__main-text-block {
		margin-bottom: 60px;

		@include max-w(575) {
			margin-bottom: 40px;
		}
	}
}