.social-buttons-list {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;

	@include max-w(767) {
		justify-content: center;
	}

	.btn {
		width: 25%;

		@include max-w(767) {
			min-width: 170px;
			margin: 5px;
		}
	}
}