.price-text {
	&__currency {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 1.875rem;
		letter-spacing: 0.15px;

		@include max-w(767) {
			font-size: 1.5rem;
		}
	}

	&__value {
		font-family: 'Montserrat', sans-serif;
		font-weight: 500;
		font-size: 4.0625rem;
		letter-spacing: 0.325px;
		line-height: 1;

		@include max-w(991) {
			font-size: 3rem;
		}

		@include max-w(767) {
			font-size: 2rem;
		}
	}

	&__period {
		color: #a1a1a1;
	}
}