.services-list {
	&__item {
		margin-bottom: 30px;
	}
}

.services-list-item {
	position: relative;
	padding-left: 100px;

	@include max-w(575) {
		padding-left: 75px;
	}

	&__icon {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.services-list-icon {
	width: 75px;
	height: 75px;
	border-radius: 50%;
	background-color: $blue;

	@include max-w(575) {
		width: 60px;
		height: 60px;
	}

	> .animation-svg {
		width: 38px;
		@include absolute-center;

		@include max-w(575) {
			width: 30px;
		}
	}
}