.logo-item {
	max-width: 185px;
	font-size: 0;
	margin-bottom: 100px;

	@include max-w(991) {
		margin-bottom: 70px;
		max-width: 100px;
	}

	@include max-w(767) {
		margin-bottom: 50px;
	}

	@include max-w(575) {
		margin-bottom: 30px;
	}
}

.logo-item-wrap {
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: center;
}