// MAIN TITLE OF THE SITE =========================================
.main-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	letter-spacing: 0.24px;
	font-size: 3rem;
	color: $white;
	margin-bottom: 20px;

	span {
		font-family: 'Playfair Display', serif;
	}

	@include max-w-and-max-h(1199, 770) {
		font-size: 2.5rem;
		margin-bottom: 15px;
	}

	@include max-w(991) {
		font-size: 2.2rem;
	}

	@include max-w(767) {
		font-size: 2rem;
		margin-bottom: 10px;
	}

	@include max-w(575) {
		font-size: 1.7rem;
	}
}

// SECTION TITLE =========================================
.section-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;

	&.-small {
		font-size: 1.875rem;
		letter-spacing: 0.15px;
		margin-bottom: 20px;

		> i {
			font-family: 'Playfair Display', serif;
		}

		@include max-w(991) {
			font-size: 1.6rem;
		}

		@include max-w(767) {
			margin-bottom: 15px;
		}

		@include max-w(575) {
			font-size: 1.3rem;
		}
	}

	&.-big {
		font-size: 2.5rem;
		letter-spacing: 0.2px;
		margin-bottom: 10px;

		@include max-w(991) {
			font-size: 2.2rem;
		}

		@include max-w(767) {
			font-size: 2rem;
		}

		@include max-w(575) {
			font-size: 1.5rem;
		}
	}
}

// SINGLE POST TITLE
.single-post-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	font-size: 2.375rem;
	margin-bottom: 20px;

	@include max-w(575) {
		font-size: 1.6rem;
		margin-bottom: 10px;
	}

	> i {
		font-family: 'Playfair Display', serif;
	}
}

// SMALL TITLE =========================================
.small-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	margin-bottom: 10px;
	letter-spacing: 0.16px;
}

// EXTRA SMALL TITLE
.extra-small-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 0.75rem;
	text-transform: uppercase;
	letter-spacing: 0.9px;
}

// BLOCK TITLE =========================================
.block-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 1.375rem;
	letter-spacing: 0.11px;
	margin-bottom: 20px;
	line-height: 1.4;

	@include max-w(575) {
		margin-bottom: 10px;
		font-size: 1.2rem;
	}

	i {
		font-family: 'Playfair Display', serif;
		font-weight: 700;
	}
}

// BLOCK TITLE =========================================
.title-with-number {
	position: relative;
	padding-left: 90px;
	margin-bottom: 25px;

	@include max-w(575) {
		padding-left: 70px;
		margin-bottom: 15px;
	}

	&__number {
		font-family: 'Montserrat', sans-serif;
		font-size: 3.125rem;
		letter-spacing: 0.25px;
		line-height: 1;
		color: $primary;
		position: absolute;
		top: 0;
		left: 0;

		@include max-w(575) {
			font-size: 2.5rem;
		}
	}

	&__text {
		color: #101010;
	}
}

.text-lg {
	font-size: 1.125rem;
	letter-spacing: 0.18px;

	p {
		margin-bottom: 30px;

		@include max-w(575) {
			margin-bottom: 15px;
		}
	}

	@include max-w(575) {
		font-size: 1rem;
	}
}

.gray-description {
	font-size: 1.125rem;
	font-style: italic;
	color: $secondary;
	margin-bottom: 75px;

	@include max-w(991) {
		margin-bottom: 50px;
	}

	@include max-w(575) {
		margin-bottom: 20px;
	}
}

.medium-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 1rem;
	letter-spacing: 0.16px;
	margin-bottom: 10px;
}

.brand-title {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-size: 1.125rem;
	margin-bottom: 20px;

	@include max-w(575) {
		margin-bottom: 10px;
	}
}

.link-list {
	&.text-light-gray {
		a {
			color: $gray-200;

			&:hover {
				color: $white;
			}
		}
	}

	&.white-text {
		a {
			color: $white;
		}
	}
}