.hamberger-btn {
	width: 46px;
	height: 46px;
	position: relative;
	margin-left: 15px;
	cursor: pointer;

	&__icon {
		position: absolute;
		height: 100%;
		width: 100%;

		span {
			width: 36px;
			height: 2px;
			position: relative;
			top: 5px;
			left: 5px;
			margin: 7px 0;

			&:nth-child(1) {
				transition-delay: .5s;
			}

			&:nth-child(2) {
				transition-delay: .625s;
			}
			&:nth-child(3) {
				transition-delay: .75s;
			}
		}
	}

	&__cross {
		position: absolute;
		height: 100%;
		width: 100%;
		transform: rotate(45deg);

		span {
			&:nth-child(1) {
				height: 0%;
				width: 2px;
				position: absolute;
				top: 5px;
				left: 22px;
				transition-delay: 0s;
			}

			&:nth-child(2) {
				width: 0%;
				height: 2px;
				position: absolute;
				left: 5px;
				top: 22px;
				transition-delay: .25s;
			}
		}
	}

	span {
		display: block;
		background: $white;
		transition: 0.5s;
	}

	&.-open {
		.hamberger-btn__icon {
			span {
				width: 0%;

				&:nth-child(1) {
					transition-delay: 0s;
				}

				&:nth-child(2) {
					transition-delay: .125s;
				}

				&:nth-child(3) {
					transition-delay: .25s;
				}
			}
		}

		.hamberger-btn__cross {

			span {
				&:nth-child(1) {
					height: 80%;
					transition-delay: .625s;
				}
			}

			span {
				&:nth-child(2) {
					width: 80%;
					transition-delay: .375s;
				}
			}
		}
	}
}